import * as Yup from 'yup';

const regx = {
    nameCheck: /^^[a-zA-Z- -0-9]{6,20}$/,
    sum: null,
}

const nameCheck = Yup.string().matches(regx.nameCheck, " 6  20 ").required(" ")
const sum = Yup.number().typeError("  ").required(" ").min(1, "   ");

export const schemas = {
    custom: Yup.object().shape({
        nameCheck,
        sum
    })
}
export const initialValues = {
    nameCheck: "",
    sum: "",
    date: ""
}